import { supabase } from "@/lib/supabase";

export default async function AdminPage() {
  // Fetch all orders
  const { data: orders, error } = await supabase
    .from("orders")
    .select("*")
    .order("created_at", { ascending: false });

  if (error) {
    return <div>Error loading orders: {error.message}</div>;
  }

  return (
    <div className="container-page py-10">
      <h1 className="text-3xl font-bold mb-6">All Orders</h1>

      <div className="space-y-6">
        {orders?.map((order) => (
          <div
            key={order.id}
            className="border rounded-xl p-5 bg-white shadow-soft"
          >
            <div className="flex justify-between items-center">
              <h2 className="text-xl font-semibold">Order #{order.id}</h2>
              <span className="text-sm bg-neutral-200 px-3 py-1 rounded-full">
                {order.status}
              </span>
            </div>

            <p className="text-neutral-700 mt-1">
              <strong>Name:</strong> {order.name}
            </p>
            <p className="text-neutral-700">
              <strong>Email:</strong> {order.email}
            </p>
            <p className="text-neutral-700 mb-3">
              <strong>Placed:</strong>{" "}
              {new Date(order.created_at).toLocaleString()}
            </p>

            <h3 className="font-semibold">Items:</h3>
            <ul className="ml-5 list-disc mt-2 space-y-1">
              {order.items.map((entry: any, idx: number) => (
                <li key={idx}>
                  {entry.item.name} × {entry.qty} — $
                  {(entry.item.price * entry.qty).toFixed(2)}
                </li>
              ))}
            </ul>

            <div className="mt-4 font-bold">
              Total: $
              {order.items
                .reduce(
                  (sum: number, entry: any) =>
                    sum + entry.item.price * entry.qty,
                  0
                )
                .toFixed(2)}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
